.navbar-container {
    margin-bottom: 120px;
    #mainNav {
        background-color: var(--primary-color);
        height: 50px;
        margin-top: 80px;
        box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.25);

        #menu-button {
            outline: none;
            background: transparent;
            padding: 3px;

            i {
                font-size: 18px;
            }
        }

        #navbar-menu-list {
            display: flex;
            width: 500px !important;
            justify-content: space-around;
        }
        .nav-link:hover {
           color: cyan !important;
       }
    }
}