.job-info-main-outer {
  padding: 3rem 0;
  .container {
    .row {
      .job-block {
        border-radius: 7px;
        box-shadow: 0 0 35px rgba(0, 0, 0, 0.1);
        padding: 1rem;
        margin-bottom: 1rem;
        background-color: #fff !important;
        h4{
          font-weight: 400 !important;
          text-align: left;
          text-transform: capitalize;
          color: var(--secondary-color);
        }
        p{
          margin-bottom: 0;
        }
        span{
          display: flex;
          color: slategray;
        }
        height: 6rem;
      }
    }
  }
}
