@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
.footer-container {        
    background-color: #000;

    .contact-address {
        color: white;
        margin-bottom: 20px;

        p {
            text-align: center;
            font-family: 'Lato', sans-serif;
            font-size: 16px;
        }

        h2 {
            color: #fed136;
            margin-bottom: 15px;
            font-family: sans-serif;
            font-size: 22px;
        }
    }
    .list-inline-item{
        a:hover{
            background-color: var(--primary-color);
        }
        a:active {
            background-color: var(--primary-color);
        }
    }

    .copyright {
        color: white;
    }
}