.heading {
  text-align: center;
}
.heading h1 {
  font-size: 30px !important;
  color: var(--secondary-color);
  position: relative;
  text-transform: capitalize;
  font-family: Montserrat,-apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol','Noto Color Emoji';
  font-size: 30px;
}
.main-info {
  background-color: #fff;
  padding: 2rem;
  .list .num {
    padding: 0.5rem 2rem;
    display: -webkit-box;
    // display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-transition: 0.25s;
    transition: 0.25s;
    h3 {
      position: relative;
      left: -1.5rem;
      color: #3d3d3d;
      font-size: 0.85rem;
      -webkit-transition: 0.25s;
      transition: 0.25s;
    }
  }
  @media (max-width: 770px) {
    margin-right: 0 !important;
    padding: 0 !important;
  }
}

.main-info .list .num:before
 {
  font-size: 1.5rem;
  font-weight: bold;
  color: #000;
  width: 2rem;
  opacity: 0.05;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  content: "";
}
.main-info .list .num:hover {
  background-color: #fafafa;
  cursor: pointer;
}
.main-info .list .num:hover:before {
  opacity: 0.4;
}
.main-info .list .num:hover h3 {
  left: 1rem;
}
.message {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin-top: 2rem;
}
.info-icons{
  font-weight: bold;
  color: green;
  margin-right: 1.5rem;
}
.underline{
  width: 10rem;
  height: 2px;
  background: gold;
  margin: .7rem auto;
}
.exit-icon{
  font-weight: bold;
  font-size: 2rem;
  color: rgb(3,90,161);
  margin-right: 1.5rem;
  cursor: pointer;
}
