#get-started .feature-block h4 {
  margin-top: 10px;
  margin-bottom: 5px;
  text-transform: capitalize;
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

#get-started .feature-block p {
  margin-bottom: 20px;
  font-size: 14px;
}

#get-started .feature-block a {
  color: #505050;
  font-size: 12px;
  font-weight: 600;
  text-decoration: none;
  text-transform: capitalize;
  transition: 0.3s;
  cursor: pointer;
}

#get-started .feature-block a:hover {
  color: blue;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal !important;
}
.underline{
  width: 10rem;
  height: 2px;
  background: gold;
  margin: .7rem auto;
}