.heading{
  padding-top: 30px;
  text-size-adjust: 12;
}

.page-section {
    padding: 100px 0;
  }
  
  .container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 30px;
    margin-right: auto;
    margin-left: auto;
  }
  
  .about {
    width: 100%;
    text-align: justify;
    margin: auto;
  }
  
  .section-heading {
    margin: 10px;
    text-align: left;
    font-size: 18px;
    color: black;
    text-size-adjust: auto;
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
  }
  
  .col-lg-12 {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
  }
  
  .rounded-circle {
    border-radius: 50%!important;
   }
  
  .img-fluid {
    max-width: 100%;
    height: auto;
  }
  
  img {
    vertical-align: middle;
    border-style: none;
  }

  .about-subheading {
    font-weight: 500 !important;
  }
  
